	module.exports = {"data":{"markdownRemark":{"html":"<h2>Agile</h2>\n<p>Agile software development is a collection of methodologies used to manage teams of developers. It advocates adaptive planning, evolutionary development, early delivery, and continuous improvement, and it encourages rapid and flexible response to change. People and communication are considered more important than tools and processes.</p>\n<p>Agile emphasizes asking end users what they want, and frequently showing them demos of the product as it is developed. This stands in contrast to the “Waterfall” approach, specification-driven development, and what Agile practitioners call “Big Up-Front Design.” In these approaches, the features are planned out and budgeted before development starts. </p>\n<p>With Agile, the emphasis is on “agility” - being able to quickly respond to feedback from users and other changing circumstances.</p>\n<p><img src=\"https://www.commitstrip.com/wp-content/uploads/2017/01/Strip-Budegt-fixe-pour-projet-flexible-english650-final.jpg\" alt=\"A comic from Commitstrip.com showing a product manager explaining to a developer that they are switching to agile, but then asking the developer to plan everything up front\"></p>\n<p>There are many different flavors of agile, including Scrum and Extreme Programming.</p>\n<h3>More information</h3>\n<p><a href=\"https://www.agilealliance.org/\">Agile Alliance’s Homepage</a></p>","fields":{"slug":"/agile/"},"frontmatter":{"title":"Agile"}}},"pathContext":{"slug":"/agile/"}}

